import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { scrollSmooth } from '../../utilities/helper'

import './style.scss'

export default class BackToTop extends Component {
	constructor(props) {
		super(props)
		this.state = {
			is_scroll: false,
		}
	}

	componentDidMount() {
		window.addEventListener("scroll", e => {
			this.setState({
				is_scroll: window.pageYOffset > 500,
			})
		})
	}
  render() {


    return (
      <div className={`back-to-top-component ${this.state.is_scroll ? "" : "active"}`} onClick={scrollSmooth(0)}>
        <span><FontAwesomeIcon icon={['fas', 'angle-up']} /></span>
      </div>
    );
  }
}

BackToTop.propTypes = {
};