import React from "react";
import {
	makeLink,
} from "../../utilities/helper"
import host_url from "../../utilities/constant"
import { CS } from "../../share/ColorScheme"
import './style.scss';

export default class HomePage extends React.Component {
	constructor(props) {
		super(props);
		this.is_mount = true;
		this.image_examples = [
			'dog-face',
			'baby',
			'tree',
			'avocado',
			'girl',
		]
		this.state = {
			current_image: 'dog-face',
		}
	}

	componentDidMount() {
	}

	componentWillUnmount() {
		this.is_mount = false;
	}

	setImage = img => {
		return e => {
			this.setState({
				current_image: img,
			})
		}
	}

	render() {
		let image_selection_ele;
		image_selection_ele = this.image_examples.map((img, i) => {
			return <li key={i} className={this.state.current_image === img ? 'active': ''}>
				<figure onClick={this.setImage(img)}>
					<img src={`images/original/${img}.jpg`} alt={img}/>
				</figure>
			</li>
		})


		let color_figure_eles = [];
		for (let color_code in CS) {
			let colors = [];
			let scheme = CS[color_code].scheme;

			for (let i = 0; i < scheme.length; i++) {
				colors.push(<div key={i} className="ibc">
					<span style={{backgroundColor: `#${scheme[i][1] || scheme[i][0]}`}}></span>
				</div>)
			}
			color_figure_eles.push(<li key={color_code}>
				<div className="li-wrapper">
					<figure>
						<img src={`images/${this.state.current_image}/${this.state.current_image}-${color_code}.png`} loading="lazy"/>
					</figure>
					<div className="color-wrapper ibc">
						{colors}
					</div>
					<div className="code">
						<p>Color Code: <strong>{color_code.split('color_code_')[1]}</strong></p>
					</div>
				</div>
			</li>)
		}

		return(
			<div className="home-page">
				<div className="images-wrapper">
					<div className="image-header">
						<ul className="ibc">
							{image_selection_ele}
						</ul>
					</div>
					<div className="image-header-clone"></div>
					<div className="image-list">
						<ul className="ibc">
							{color_figure_eles}
						</ul>
					</div>
				</div>
			</div>
		);
	}
}