import React from 'react';
import { Switch, Route } from 'react-router-dom';
import HomePageMain from '../container/home-page/HomePageMain'
import NotFound404 from '../container/not-found-404/NotFound404'
// import ExperimentPageMain from '../container/experiment-page/ExperimentPageMain'
import { history } from "./store";
import { ConnectedRouter as Router } from "react-router-redux";

export const path = {
	home: "/",
	notFound404: "/not-found-404", 
	// experiment: "/experiment", 
}
	
let Routes = {
	Base: (
		<Router history={history}>
			<Switch>
				<Route exact path={`${path.home}`} component={HomePageMain}/>
				<Route exact path={`${path.notFound404}`} component={NotFound404}/>
				<Route component={NotFound404} />
			</Switch>
	  </Router>
	),

}

export default Routes;