// /utilities/helper

import React from 'react';
import { Link } from 'react-router-dom';
import { ConnectedRouter as Router, push } from "react-router-redux";
import { history, router_store } from "../router/store";
import NotFound404 from '../container/not-found-404/NotFound404'
import LoadingFullPage from '../components/LoadingFullPage/LoadingFullPage';
import LoadingCircle from "../components/LoadingCircle/LoadingCircle";

export const routeTo = (pathname, name) => {
	let location = {
		pathname,
		state: {
			name
		}
	}
	return e => {
		router_store.dispatch(push(location));
		window.scrollTo(0, 0);
	}
}

export const makeLink = (pathname, label, target) => {
	let clickLink;
	if (!target) {
		clickLink = e => {
			window.scrollTo(0, 0);
		}
	}
	return <Router history={history}>
		<Link to={pathname} target={target} onClick={clickLink}>{label}</Link>
  </Router>
}

export const validateEmptyField = input => {
	let is_pass = input && input !== "";
	return is_pass;
}

export const validateEmailField = input => {
  let email_regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  let is_pass = email_regex.test(input.toLowerCase());
  return is_pass;
}

export const validateTelField = input => {
  let tel_regex = /^[+]?[(]?[0-9]{1,4}[)]?[-\s.]?[0-9]{1,3}[-\s.]?[0-9]{1,10}$/;
	let is_pass = tel_regex.test(input.toLowerCase());
  return is_pass;
}

export const validateConfirmPasswordField = (password_1, password_2) => {
	return password_1 === password_2;
}

export const copyToClipboard = str => {
  const el = document.createElement('textarea');
  el.value = str;
  el.setAttribute('readonly', '');
  el.style.position = 'absolute';
  el.style.left = '-9999px';
  document.body.appendChild(el);
  el.select();
  document.execCommand('copy');
  document.body.removeChild(el);
};

export const makeQueryParam = (queryObj = {}) => {
	if (Object.keys(queryObj).length <= 0) return "";
	let q = "?";
	for (let key in queryObj) {
    if (queryObj.hasOwnProperty(key)) {
      q += key + "=" + queryObj[key];
      q += "&";
    }
	}
	return q.slice(0, -1);
}

