export const switchElemActive = (nodes, index = 0) => {
	if (nodes !== null && nodes.length !== 0) {
		let elems = Array.prototype.slice.call(nodes);
		elems.map(elem => {
		  elem.classList.remove('active');
			return true;
		})
		elems[index].classList.add('active');
	}
}

export const checkSuccessStatus = res => {
	return res && res.status && res.status !== -1 && res.status >= 200 && res.status < 300;
}

export const pageLoading = bool => {
	return bool ? <LoadingFullPage/> : null;
}

// parent must position relative
export const componentLoading = bool => {
	return bool ? <LoadingCircle/> : null;
}

export const refreshPage = () => {
	window.location.reload(); 
}

export const getWindowWidth = () => {
	return window.innerWidth;
}

export const handle404DynamicPage = () => {
	return (<NotFound404 />);
}

export const setCookie = (cname, cvalue, exdays) => {
    let d = new Date(); //Create an date object
    d.setTime(d.getTime() + (exdays*1000*60*60*24)); //Set the time to exdays from the current date in milliseconds. 1000 milliseonds = 1 second
    let expires = "expires=" + d.toGMTString(); //Compose the expirartion date
    window.document.cookie = cname+"="+cvalue+"; "+expires;//Set the cookie with value and the expiration date
}

export const getCookie = (cname) => {
    let name = cname + "="; //Create the cookie name var with cookie name concatenate with = sign
    let cArr = window.document.cookie.split(';'); //Create cookie array by split the cookie by ';'
     
    for(let i=0; i<cArr.length; i++) {
        let c = cArr[i].trim();
        if (c.indexOf(name) === 0) 
            return c.substring(name.length, c.length);
    }
    return "";
}

export const onPressEnterForm = (fn, state) => {
	return e => {
	  if (e.which === 13 || e.keyCode === 13) {
	    e.preventDefault();
	    e.stopPropagation();
	    fn(state);
	    return false;
	  }
  }
}

var cbfn = function( x1, x2 ) {
    return function( t ) {
        return 3 * ( 1 - t ) ** 2 * t * x1 + 3 * ( 1 - t ) * t ** 2 * x2 + t ** 3;
    }
}

var estimateY = function( P1, P2 ) {
    var tolerance = 0.0001;
    var fx = cbfn( P1[0], P2[0] );
    var fy = cbfn( P1[1], P2[1] );
    var lower;
    var upper;
    var time_percent;
    var estimateX = 0;
    return function( xFrame ) {
        lower = 0;
        upper = 1;
        do {
            time_percent = ( upper + lower ) / 2;
            estimateX = fx( time_percent );
            if ( xFrame > estimateX ) {
                lower = time_percent;
            } else {
                upper = time_percent;
            }
        } while ( Math.abs( xFrame - estimateX ) > tolerance )

        return fy( time_percent );
    }
}

var getYSet = function( P1, P2 ) {
    P1 = P1 || [.42, 0];
    P2 = P2 || [.58, 1];
    var getEstimateY = estimateY( P1, P2 );
    var set_ys = [];
    var fps = 1000 / 60;
    var frames;
    return function( duration ) {
        frames = Math.round( duration / fps );
        set_ys.length = 0;
        for ( var i = 1; i <= frames; i++ ) {
            let xFrame = i / frames;
            set_ys.push( getEstimateY( xFrame ) );
        }
        return set_ys;
    }
}

var scrollingY = function( go_y ) {
    window.scrollTo( 0, go_y );
}

var easeInOut = getYSet()(1000);

export const scrollSmooth = function( num, smooth_set_y = easeInOut ) {
    var fps = 1000 / 60;
    var to_elem_pos = num;
    var total_distance;
    var curr_pos;
    var move_step = 0;
    return function( e ) {
        curr_pos = window.pageYOffset;
        total_distance = to_elem_pos - curr_pos;
        for ( let step = 1; step <= smooth_set_y.length; step++ ) {
            // eslint-disable-next-line
            setTimeout( function() { scrollingY( curr_pos + total_distance * smooth_set_y[move_step++] ); }, fps * step );
        }
        move_step = 0;
    }
}

