export const getTime = (hours, mins, secs) => {
  if (mins != null && mins < 10) {
    mins = "0" + mins;
  }

  if (secs != null && secs < 10) {
    secs = "0" + secs;
  }

  if (hours === 0) {
    return `12${mins ? '.' + mins : ''}${secs ? '.' + secs : ''} a.m.`
  } else if (hours >= 1 && hours <= 11) {
    return `${hours}${mins ? '.' + mins : ''}${secs ? '.' + secs : ''} a.m.`
  } else if (hours === 12) {
    return `12${mins ? '.' + mins : ''}${secs ? '.' + secs : ''} p.m.`
  } else {
    return `${hours - 12}${mins ? '.' + mins : ''}${secs ? '.' + secs : ''} p.m.`
  }
}

// Format = "YYYY-mm-dd hh:mm:ss"
export const makeDate = str_data => {
  if (str_data) {
    let s_arr = [...str_data.split(" ")]
    s_arr = [...s_arr[0].split("-"), ...s_arr[1].split(":")]
    s_arr[1] -= 1
    return new Date(...s_arr);
  } else {
    return new Date();
  }
}